.home {
  padding: 10px 40px;
  display: flex;
  flex-direction: column;
  height: 75%;
  row-gap: 50px;
}

.info {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
  justify-content: center;
  align-items: center;
}

.sessions {
  display: flex;
  column-gap: 30px;
  justify-content: center;
  align-items: center;
}

.session-tab {
  height: 200px;
  aspect-ratio: 16 / 9;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  align-content: center;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  opacity: 0.5;
  text-decoration: none;
  color: black;
}

.session-tab.enabled {
  opacity: 1;
  pointer-events: all;
}
